// src/components/exercise-common/CategorySelector/index.js
import { useState, useCallback, memo, useEffect, useRef, useMemo } from "react";
import { View, ScrollView, TouchableOpacity, Text, Animated } from "react-native";
import { LinearGradient } from "expo-linear-gradient";
import PropTypes from 'prop-types';
import createStyles from "./style";

// Réfrence pour les mesures de layout du ScrollView
const itemLayouts = {};

const CategorySelector = ({
  categories = [],
  selectedCategory,
  onSelectCategory,
  primaryColor = "#5E60CE",
}) => {
  const styles = createStyles(primaryColor);
  const scrollViewRef = useRef(null);
  
  // Correction: Initialisation complète de l'objet useRef dès le départ
  // pour s'assurer que 'values' et 'press' existent toujours.
  const animationsRef = useRef({
    values: {},
    press: {},
  });

  const isAnimatingRef = useRef(false);
  const [prevSelectedCategory, setPrevSelectedCategory] = useState(selectedCategory);

  // Synchronisation des animations avec les catégories
  // Cette logique doit être dans un useEffect pour s'exécuter après le rendu
  // et ne pas créer d'animations dans le corps du composant.
  useEffect(() => {
    // Initialiser les animations qui n'existent pas encore
    for (const category of categories) {
      if (!animationsRef.current.values[category.id]) {
        animationsRef.current.values[category.id] = new Animated.Value(
          category.id === selectedCategory ? 1 : 0
        );
      }
    }
  }, [categories, selectedCategory]);


  // Cette fonction est correcte.
  const getPressAnimation = useCallback((categoryId) => {
    if (!animationsRef.current.press[categoryId]) {
      animationsRef.current.press[categoryId] = new Animated.Value(1);
    }
    return animationsRef.current.press[categoryId];
  }, []);


  const handlePressIn = useCallback((categoryId) => {
    Animated.timing(getPressAnimation(categoryId), {
      toValue: 0.95,
      duration: 150,
      useNativeDriver: true,
    }).start();
  }, [getPressAnimation]);


  const handlePressOut = useCallback((categoryId) => {
    Animated.timing(getPressAnimation(categoryId), {
      toValue: 1,
      duration: 150,
      useNativeDriver: true,
    }).start();
  }, [getPressAnimation]);

  // Cette fonction est correcte et ne nécessite pas de changement.
  const triggerAnimation = useCallback((fromCategory, toCategory) => {
    if (isAnimatingRef.current) return;
    
    isAnimatingRef.current = true;
    const animationsArray = [];

    const fromAnim = animationsRef.current.values[fromCategory];
    if (fromCategory !== undefined && fromAnim) {
      animationsArray.push(
        Animated.timing(fromAnim, {
          toValue: 0,
          duration: 200,
          useNativeDriver: false,
        })
      );
    }

    const toAnim = animationsRef.current.values[toCategory];
    if (toCategory !== undefined && toAnim) {
      animationsArray.push(
        Animated.timing(toAnim, {
          toValue: 1,
          duration: 300,
          useNativeDriver: false,
        })
      );
    }

    if (animationsArray.length > 0) {
      Animated.parallel(animationsArray).start(() => {
        isAnimatingRef.current = false;
      });
    } else {
      isAnimatingRef.current = false;
    }
  }, []);

  // Cette fonction est correcte.
  useEffect(() => {
    if (prevSelectedCategory !== selectedCategory) {
      triggerAnimation(prevSelectedCategory, selectedCategory);
      setPrevSelectedCategory(selectedCategory);
    }
  }, [selectedCategory, prevSelectedCategory, triggerAnimation]);

  // Cette fonction est correcte.
  useEffect(() => {
    if (scrollViewRef.current && selectedCategory) {
      const layout = itemLayouts[selectedCategory];
      if (layout) {
        scrollViewRef.current.scrollTo({
          x: layout.x - (layout.width / 2),
          y: 0,
          animated: true,
        });
      }
    }
  }, [selectedCategory, categories]);

  // Cette fonction est correcte.
  const handleCategoryPress = useCallback((categoryId) => {
    if (categoryId !== selectedCategory && !isAnimatingRef.current) {
      onSelectCategory(categoryId);
    }
  }, [selectedCategory, onSelectCategory]);


  // Cette fonction est correcte.
  const createPressHandler = useCallback((categoryId) => () => handleCategoryPress(categoryId), [handleCategoryPress]);


  // Utiliser useMemo pour mémoriser les éléments de catégorie
  const categoryPills = useMemo(() => {
    return categories.map((category) => {
      const isSelected = selectedCategory === category.id;
      // Correction : Supprimer la création conditionnelle d'animation ici
      // car elle est déjà gérée dans le useEffect.
      // Cette ligne ne sera plus la source d'erreur.
      const animation = animationsRef.current.values[category.id];
      const pressAnimation = getPressAnimation(category.id);

      // Le reste du code est correct
      const scale = animation ? animation.interpolate({
        inputRange: [0, 1],
        outputRange: [1, 1.05],
      }) : new Animated.Value(0); // Gérer le cas où l'animation n'est pas encore prête


      const pressScale = pressAnimation.interpolate({
        inputRange: [0.95, 1],
        outputRange: [0.95, 1],
      });

      const shadowOpacity = animation ? animation.interpolate({
        inputRange: [0, 1],
        outputRange: [0.1, 0.25],
      }) : new Animated.Value(0);

      const borderWidth = animation ? animation.interpolate({
        inputRange: [0, 1],
        outputRange: [1, 2],
      }) : new Animated.Value(0);

      return (
        <Animated.View
          key={category.id}
          onLayout={(event) => {
            itemLayouts[category.id] = event.nativeEvent.layout;
          }}
          style={[
            styles.categoryItemWrapper,
            {
              transform: [{ scale: Animated.multiply(scale, pressScale) }],
            },
          ]}
        >
          <TouchableOpacity
            style={styles.categoryTouchable}
            onPress={createPressHandler(category.id)}
            onPressIn={() => handlePressIn(category.id)}
            onPressOut={() => handlePressOut(category.id)}
            activeOpacity={0.8}
            accessibilityRole="button"
            accessibilityLabel={String(category.name)}
            accessibilityState={{ selected: isSelected }}
          >
            {isSelected ? (
              <LinearGradient
                colors={[primaryColor, `${primaryColor}E6`, `${primaryColor}CC`]}
                start={{ x: 0, y: 0 }}
                end={{ x: 1, y: 1 }}
                style={styles.selectedCategoryItem}
              >
                <View style={styles.selectedInner}>
                  <Text style={styles.selectedCategoryText}>
                    {category.name}
                  </Text>
                  <View style={styles.sparkleContainer}>
                    <Text style={styles.sparkle}>✨</Text>
                  </View>
                </View>
              </LinearGradient>
            ) : (
              <Animated.View
                style={[
                  styles.categoryItem,
                  {
                    borderWidth,
                    borderColor: `${primaryColor}20`,
                    shadowOpacity,
                  }
                ]}
              >
                <View style={[styles.categoryInner, { backgroundColor: `${primaryColor}08` }]}>
                  <Text style={[styles.categoryText, { color: primaryColor }]}>
                    {category.name}
                  </Text>
                </View>
              </Animated.View>
            )}
          </TouchableOpacity>
        </Animated.View>
      );
    });
  }, [categories, selectedCategory, primaryColor, createPressHandler, handlePressIn, handlePressOut, getPressAnimation, styles]);

  return (
    <View style={styles.container}>
      <LinearGradient
        colors={[`${primaryColor}04`, 'transparent', `${primaryColor}02`]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 0 }}
        style={styles.backgroundGradient}
      >
        <ScrollView
          ref={scrollViewRef}
          horizontal
          showsHorizontalScrollIndicator={false}
          contentContainerStyle={styles.scrollContent}
          style={styles.scrollView}
        >
          {categoryPills}
        </ScrollView>
      </LinearGradient>
    </View>
  );
};

CategorySelector.propTypes = {
  categories: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
    name: PropTypes.string.isRequired,
  })).isRequired,
  selectedCategory: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onSelectCategory: PropTypes.func.isRequired,
  primaryColor: PropTypes.string,
};

export function areEqual(prevProps, nextProps) {
  const categoriesEqual = prevProps.categories === nextProps.categories ||
    (prevProps.categories.length === nextProps.categories.length &&
      prevProps.categories.every((cat, index) => cat.id === nextProps.categories[index].id));

  return (
    prevProps.selectedCategory === nextProps.selectedCategory &&
    prevProps.primaryColor === nextProps.primaryColor &&
    categoriesEqual
  );
}

export default memo(CategorySelector, areEqual);